html {
  font-family: 'Raleway', sans-serif;
  background-color: #0B0C10;
  color: #FFFFFF; }

.container {
  width: 75%;
  max-width: 1440px;
  margin: 0 auto; }
  @media (max-width: 1440px) {
    .container {
      width: 85%; } }
  @media (max-width: 960px) {
    .container {
      width: 90%; } }
  @media (max-width: 640px) {
    .container {
      width: 100%;
      padding-left: 20px;
      padding-right: 20px; } }

a {
  text-decoration: none;
  color: #66FCF1;
  transition: .3s; }
  a:hover {
    color: #45A29E; }

.btn {
  color: #FFFFFF;
  border: 2px solid #66FCF1;
  padding: 20px;
  text-transform: uppercase;
  font-weight: 600;
  transition: .3s; }
  .btn:hover {
    background-color: #45A29E;
    border: 2px solid #45A29E;
    color: #1F2833; }

.flex {
  display: flex; }

.grid {
  display: grid; }

.lazy {
  opacity: 0;
  transition: opacity 3s; }
  .lazy.loaded {
    opacity: 1; }

.navbar {
  padding: 30px 0;
  overflow: hidden;
  transition: .3s;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
  box-shadow: 0 8px 6px -6px #66FCF1;
  background-color: rgba(11, 12, 16, 0.9); }
  .navbar .wrapper {
    justify-content: space-between; }
    .navbar .wrapper .logo {
      margin: auto 0;
      font-weight: 600;
      font-size: 1.8rem;
      transition: .3s; }
    .navbar .wrapper .contact-info .phone {
      font-size: 1.4rem;
      font-weight: 600;
      transition: .3s; }
    .navbar .wrapper .contact-info .address {
      transition: .3s; }

.hero {
  background-image: url("../img/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  position: relative;
  height: 100vh; }
  .hero:before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #0B0C10;
    opacity: .85; }
  .hero .container {
    position: relative;
    z-index: 2; }
    .hero .container h1 {
      font-size: 3rem;
      font-weight: 700;
      width: 80%;
      color: #66FCF1;
      padding-top: 200px;
      padding-bottom: 120px;
      text-transform: uppercase; }
  .hero__content {
    justify-content: space-between; }
    .hero__content .blocks {
      width: 35%; }
      .hero__content .blocks .block {
        padding-bottom: 40px;
        font-size: 1.6rem;
        line-height: 2.4rem; }
        .hero__content .blocks .block .percent {
          font-size: 2.2rem; }
          .hero__content .blocks .block .percent span {
            font-size: 3rem;
            font-weight: 400; }
    .hero__content .button {
      align-items: flex-end;
      padding-bottom: 40px; }

.offer {
  height: 40vh;
  padding: 50px 0; }
  .offer__content {
    height: 100%;
    flex-direction: column;
    justify-content: space-between; }
  .offer .container {
    height: 100%; }

.objects {
  padding: 100px 0; }
  .objects .wrapper {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template: 1fr 1fr;
    gap: 30px;
    grid-template-areas: "accommodation business land" "industrial industrial industrial"; }
    .objects .wrapper__accommodation {
      grid-area: accommodation; }
    .objects .wrapper__business {
      grid-area: business; }
    .objects .wrapper__land {
      grid-area: land; }
    .objects .wrapper__industrial {
      grid-area: industrial; }
    .objects .wrapper .item {
      height: 300px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 100% 100%; }
      .objects .wrapper .item._long {
        background-position: 100% 40% !important; }
    .objects .wrapper a {
      position: relative; }
      .objects .wrapper a:after {
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background-color: #1F2833;
        opacity: 0;
        transition: .3s; }
      .objects .wrapper a:hover:after {
        opacity: .7; }

.carousel__title {
  font-size: 2rem; }

.carousel__logos {
  margin: 100px 0; }
  .carousel__logos img {
    width: 200px;
    height: 200px; }
  .carousel__logos svg {
